.navbar-container {
  background: transparent;
  background-color: transparent;
  width: 100%;
  height: 40px;
  justify-content: center;
  align-items: center;
}
.navbar-container ul {
  list-style: none;
  display: flex;
}

.navbar-container ul li a {
  text-decoration: none;
  margin: 10px;
  color: #fff;
  font-size: 16px;
  font-weight: bold;
}
